<template>
  <div v-if="this.games.length" class="gameslist">
    <template v-for="game in games" :key="game.gameId">
      <div class="game">
        <ShowGame :gameId="game.gameId" />
      </div>
    </template>
  </div>
  <p v-else>There are no games played at the moment</p>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import ShowGame from "@/components/ShowGame.vue";
import { Game } from "@/types";

export default defineComponent({
  name: "Games",
  components: {
    ShowGame
  },
  data() {
    return {
      games: [] as Game[]
    };
  },
  mounted() {
    // important: subscribe first before http request

    // receive updates about new games
    this.$websocket.subscribe("/topic/onnewgame", update => {
      const game: Game = JSON.parse(update.body);
      this.games.unshift(game);
      this.games = this.games.slice(0, 16); // Show a maximum of 16 games, so we don't eat people's RAM.
    });

    // receive updates about finished games
    // TODO: not removing game, but adding winner (+ endscore?)
    this.$websocket.subscribe("/topic/ongameended", update => {
      const gameId: number = JSON.parse(update.body);
      const game = this.games.find(g => g.gameId == gameId);
      if (game !== undefined) {
        game.gameEnded = true;
      }
    });

    // get the full games list once using a HTTP request:
    this.$http
      .get("/gamelist")
      .then(res => {
        this.games = res.data.reverse();
      })
      .catch(console.error);
  },
  beforeUnmount() {
    this.$websocket.unsubscribe("/topic/onnewgame");
    this.$websocket.unsubscribe("/topic/ongameended");
  }
});
</script>

<style scoped lang="scss">
a {
  text-decoration: none;
}

.gameslist {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  grid-template-rows: repeat(auto-fill, minmax(400px, 1fr));
  grid-gap: 1em;
}
.game {
  display: inline-block;
}
</style>
