<template>
  <div v-if="games.length">
    <table class="center" border="0" cellspacing="0" cellpadding="0">
      <tr>
        <th>Game type</th>
        <th>White / cross</th>
        <th>Black / nought</th>
        <th>Watch game</th>
      </tr>
      <tr v-for="game in games" :key="game.gameId">
        <td>{{ game.gameType }}</td>
        <td>{{ game.playerWhite.name }}</td>
        <td>{{ game.playerBlack.name }}</td>
        <td>
          <button
            v-if="!game.gameEnded"
            class="emoji"
            v-on:click="showGame(game)"
          >
            ðŸŽ²
          </button>
        </td>
      </tr>
    </table>
  </div>

  <p v-else></p>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import { Game } from "@/types";

export default defineComponent({
  name: "CurrentGamesList",
  components: {},
  props: {
    showTable: {
      type: Boolean,
      required: true
    }
  },
  data() {
    return {
      games: [] as Game[]
    };
  },
  methods: {
    showGame(game: Game) {
      this.$router.push({ name: "Game", params: { gameId: game.gameId } });
    }
  },
  mounted() {
    // important: subscribe first before http request

    // receive updates about new games
    this.$websocket.subscribe("/topic/onnewgame", update => {
      const game: Game = JSON.parse(update.body);
      this.games.push(game);
    });

    // receive updates about finished games
    // TODO: not removing game, but adding winner (+ endscore?)
    this.$websocket.subscribe("/topic/ongameended", update => {
      const gameId: number = JSON.parse(update.body);
      const game = this.games.find(g => g.gameId == gameId);
      if (game !== undefined) {
        game.gameEnded = true;
      }
    });

    // get the full games list once using a HTTP request:
    this.$http
      .get("/gamelist")
      .then(res => (this.games = res.data))
      .catch(console.error);
  },
  beforeUnmount() {
    this.$websocket.unsubscribe("/topic/onnewgame");
    this.$websocket.unsubscribe("/topic/ongameended");
  }
});
</script>

<style scoped lang="scss">
a {
  text-decoration: none;
}

.gameslist {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  grid-template-rows: repeat(auto-fill, minmax(400px, 1fr));
  grid-gap: 1em;
}
</style>
