    <CurrentGamesList />
    <ShowGames />
  </template>
  <p v-else>
    Sorry, the server is offline at the moment. Please try again later.
  </p>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import CurrentGamesList from "@/components/CurrentGamesList.vue";
import ShowGames from "@/components/ShowGames.vue";
import store from "@/store";

export default defineComponent({
  name: "Games",
  components: {
    CurrentGamesList,
    ShowGames
  },
  computed: {
    websocketConnected() {
      return store.getters.websocketConnected;
    }
  },
  mounted() {
    document.title = "Game Server | Games";
  }
});
</script>
