
import { defineComponent } from "vue";
import { Game } from "@/types";

export default defineComponent({
  name: "CurrentGamesList",
  components: {},
  props: {
    showTable: {
      type: Boolean,
      required: true
    }
  },
  data() {
    return {
      games: [] as Game[]
    };
  },
  methods: {
    showGame(game: Game) {
      this.$router.push({ name: "Game", params: { gameId: game.gameId } });
    }
  },
  mounted() {
    // important: subscribe first before http request

    // receive updates about new games
    this.$websocket.subscribe("/topic/onnewgame", update => {
      const game: Game = JSON.parse(update.body);
      this.games.push(game);
    });

    // receive updates about finished games
    // TODO: not removing game, but adding winner (+ endscore?)
    this.$websocket.subscribe("/topic/ongameended", update => {
      const gameId: number = JSON.parse(update.body);
      const game = this.games.find(g => g.gameId == gameId);
      if (game !== undefined) {
        game.gameEnded = true;
      }
    });

    // get the full games list once using a HTTP request:
    this.$http
      .get("/gamelist")
      .then(res => (this.games = res.data))
      .catch(console.error);
  },
  beforeUnmount() {
    this.$websocket.unsubscribe("/topic/onnewgame");
    this.$websocket.unsubscribe("/topic/ongameended");
  }
});
